"use client";
import React, { useState } from "react";
import { useRouter } from "next/navigation";
import api from "@/services/backend-api/authApi"

const LoginForm: React.FC = () => {
  const router = useRouter();
  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const data = await api.signin(formData);
      localStorage.setItem("userToken", data.auth_token);
      router.push("/operations");
    } catch (error) {
      setError(error as string);
      console.error(error);
    }
  };
  const handleRegister = () => {
    router.push("/api/sign_up");
  };

  return (
    <div className="text-mainText w-full">
      <form
        onSubmit={handleSubmit}
      >
        <div>
          <label className="block text-sm font-bold mb-2" htmlFor="email">
            Email
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
            id="email"
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </div>
        <div className="mb-6">
          <label className="block text-sm font-bold mb-2" htmlFor="password">
            Пароль
          </label>
          <input
            className="appearance-none border-b-2 border-b-mainText bg-background w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
            id="password"
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            required
          />
        </div>
        {error && (
          <div className="text-red-500 text-xs italic mb-4">{error}</div>
        )}
        <div className="flex items-center justify-end">
          <button
            className="mx-4 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="submit"
          >
            Войти
          </button>
          <button
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
            type="button"
            onClick={handleRegister}
          >
            Зарегистрироваться
          </button>
        </div>
      </form>
    </div>
  );
};

export default LoginForm;
